"use client";
import { useState } from "react";

export default function UploadEventForm() {
  const [formData, setFormData] = useState({
    picture: null, heading: "", date: "", location: "", shortBrief: "", description: "", video1: "", video2: "", image1: null, image2: null
  });

  const handleChange = (e) => {
    setFormData(prev => ({ ...prev, [e.target.name]: e.target.value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("Event data:", formData);
  };

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Upload Events</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li><a href="/admin" className="hover:underline text-gray-600">Home</a></li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">Upload Events</li>
          </ol>
        </nav>
      </div>

      <div className="bg-white rounded-xl shadow-sm border p-8">
        <h2 className="text-xl font-semibold text-blue-900 mb-6">Upload Events On Website</h2>

        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <label className="block text-sm font-medium text-red-600 mb-2">Upload Event Picture <span className="text-gray-500">(530 x 398 px)</span></label>
            <input type="file" className="border rounded-lg px-3 py-2 w-full text-sm"/>
          </div>

          <div>
            <label className="block text-sm font-medium text-green-600 mb-2">Event Heading</label>
            <input type="text" name="heading" value={formData.heading} onChange={handleChange} placeholder="Enter The Heading Of the Event" className="border rounded-lg px-4 py-2 w-full text-sm"/>
          </div>

          <div className="grid grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-green-600 mb-2">Event Date</label>
              <input type="date" name="date" value={formData.date} onChange={handleChange} className="border rounded-lg px-4 py-2 w-full text-sm"/>
            </div>
            <div>
              <label className="block text-sm font-medium text-green-600 mb-2">Event Short Brief</label>
              <input type="text" name="shortBrief" value={formData.shortBrief} onChange={handleChange} placeholder="Enter Event Short Brief Here" className="border rounded-lg px-4 py-2 w-full text-sm"/>
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-green-600 mb-2">Event Location</label>
            <input type="text" name="location" value={formData.location} onChange={handleChange} placeholder="Enter Event Location" className="border rounded-lg px-4 py-2 w-full text-sm"/>
          </div>

          <div>
            <label className="block text-sm font-medium text-green-600 mb-2">Event Description</label>
            <div className="border rounded-lg p-2">
              <div className="flex items-center gap-2 mb-2 pb-2 border-b">
                <select className="text-sm border rounded px-2 py-1">
                  <option>Normal</option>
                </select>
                <button type="button" className="p-1 hover:bg-gray-100 rounded font-bold">B</button>
                <button type="button" className="p-1 hover:bg-gray-100 rounded italic">I</button>
                <button type="button" className="p-1 hover:bg-gray-100 rounded underline">U</button>
              </div>
              <textarea name="description" value={formData.description} onChange={handleChange} rows={6} className="w-full px-2 py-1 focus:outline-none text-sm"/>
            </div>
          </div>

          <div className="grid grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-blue-600 mb-2">Upload YT 1 Video Link</label>
              <input type="url" name="video1" value={formData.video1} onChange={handleChange} placeholder="Paste Link : E.g https://www.youtube.com/embed/qfeIymEYqT" className="border rounded-lg px-4 py-2 w-full text-sm"/>
            </div>
            <div>
              <label className="block text-sm font-medium text-blue-600 mb-2">Upload YT 2 Video Link</label>
              <input type="url" name="video2" value={formData.video2} onChange={handleChange} placeholder="Paste Link : E.g https://www.youtube.com/embed/qfeIymEYqT" className="border rounded-lg px-4 py-2 w-full text-sm"/>
            </div>
          </div>

          <div className="grid grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-blue-600 mb-2">Upload image 1</label>
              <input type="file" className="border rounded-lg px-3 py-2 w-full text-sm"/>
            </div>
            <div>
              <label className="block text-sm font-medium text-blue-600 mb-2">Upload image 2</label>
              <input type="file" className="border rounded-lg px-3 py-2 w-full text-sm"/>
            </div>
          </div>

          <div className="flex items-center gap-2">
            <input type="checkbox" id="agree" className="w-4 h-4"/>
            <label htmlFor="agree" className="text-sm text-green-600">I agree I want To Upload The Above Data On website</label>
          </div>

          <button type="submit" className="px-6 py-2 bg-blue-600 text-white text-sm font-medium rounded-lg hover:bg-blue-700">Submit</button>
        </form>
      </div>
    </>
  );
}







